import React, {Component} from "react";
import {connect} from "react-redux";
import {
    changeGrantorFormField,
    changeRepresentativeCountry,
    changeRepresentativeFormField,
    changeRepresentativeFormFieldNoValidations,
    removeGrantor,
    removeRepresentative,
    setGrantingDate,
    setRepresentationMode
} from "../../actions/app";
import {
    getCustomerData,
    getGrantingDate,
    getGrantingDateErrors,
    getGrantorsFormData,
    getRepresentationMode,
    getRepresentativesFormData,
    isRepresentativeDataFilled,
    isRepresentativeDataValid
} from "../../selectors";
import {registerNextStepCondition, requestCartRepresentativeData,} from "../../actions/data";
import MulticartValidationComponent from "../MulticartValidationComponent";
import {OraAddBar} from "eshop/components/OraCommonComponents";
import RepresentationMode from "./RepresentationMode";
import Representatives from "./Representatives";
import Grantors from "./Grantors";
import OraTooltip from "../../../core/components/ui/Tooltip";

// minRC: minRepresentativeCount
// maxRC: maxRepresentativeCount
// minRMC: minGrantorCount
// maxRMC: maxGrantorCount
class RepresentativeDataComponentView extends Component {

    constructor(props) {
        super(props);
        this.addRepresentative = this.addRepresentative.bind(this);
        this.addGrantor = this.addGrantor.bind(this);
    }

    modesConfig() {
        return [
            {mode: "JR",  minRC:1,    maxRC:1,                                   minRMC:0,    maxRMC:0                          },
            {mode: "WR",  minRC:2,    maxRC:this.props.maxRepresentativesCount,   minRMC:0,    maxRMC:0                          },
            {mode: "JP",  minRC:1,    maxRC:1,                                   minRMC:1,    maxRMC:this.props.maxGrantorsCount },
            {mode: "WP",  minRC:2,    maxRC:this.props.maxRepresentativesCount,   minRMC:1,    maxRMC:this.props.maxGrantorsCount }
        ];
    }

    modeConfig() {
        return this.modesConfig().filter(c => c.mode === this.props.representationMode)[0];
    }

    legalForm() {
        let legalForm = this.props.customer && this.props.customer.legalForm;
        return legalFormTranslator(legalForm);
    }

    validRepresentationMethods() {
        if (!this.legalForm()) {
            return [];
        } else {
            let ret = this.props.validRepresentationModes[this.legalForm()];
            if (ret) {
                return ret;
            } else {
                console.error("validRepresentationModes is not available for legalForm: " + this.legalForm());
                return [];
            }
        }
    }

    addRepresentative() {
        let nextMaxIndex = this.props.representativesFormData.length;
        this.props.setRepresentativeData({index: nextMaxIndex});
    }

    addGrantor() {
        let nextMaxIndex = this.props.grantorsFormData.length;
        this.props.setGrantorData({index: nextMaxIndex});
    }

    representationModeOptions() {
        let representationModeOptions = this.props.representationModeOptions[0] ? this.props.representationModeOptions : mockRepresentationModeOptions;
        return representationModeOptions.filter(opt => this.validRepresentationMethods().indexOf(opt.value) > -1);
    }

    componentWillMount() {
        this.props.registerValidation();
        this.props.requestCartRepresentativeData();
    }

    calculateComponentHeader = (channel) => {
        const tooltipText = `Przy podpisywaniu umowy przez pełnomocnika niezbędne jest posiadanie oryginału pełnomocnictwa
                            w formie aktu notarialnego lub pełnomocnictwa z notarialnie poświadczonym podpisem.
                            Pełnomocnictwo powinno zawierać datę udzielenia pełnomocnictwa.`;
        const isProxy = !(["JP", "WP"].indexOf(this.props.representationMode) === -1);
        if (channel === 'WWW' && isProxy)
            return (
                <OraTooltip label={this.props.descriptions.title}>{tooltipText}</OraTooltip>
            );
        else
            return (
                this.props.descriptions.title
            );
    };


    getRepresentativeLabel() {
        return {
            "JR": this.props.descriptions.representativeLabel,
            "WR": this.props.descriptions.representativeLabelPlural,
            "JP": this.props.descriptions.representativeLabelForProxy,
            "WP": this.props.descriptions.representativeLabelForProxyPlural
        }[this.props.representationMode];
    }

    render() {
        if (!this.legalForm()) {
            return null;
        }

        return (
            <div className="opl-form u-padding-top-xl">
                <div className="h2">
                    {this.calculateComponentHeader(this.props.channels.sales)}
                </div>
                {!this.props.isValid && <MulticartValidationComponent messageType="representativeData"/>}
                <div className="l-row">
                    <div className="l-col l-col-small-12 l-col-medium-6 l-col-4">
                        {this.getRepresentativeLabel() ?
                            <div className="h4">{this.getRepresentativeLabel()}</div> : null}
                        <RepresentationMode
                            defaultMode={this.props.descriptions.defaultMode}
                            options={this.representationModeOptions()}
                            onChange={this.props.setRepresentationMode}
                            selected={this.props.representationMode}
                            modesConfig={this.modesConfig()}
                        />
                        <Representatives
                            {...this.props}
                            data={this.props.representativesFormData}
                            representationMode={this.props.representationMode}
                            onChange={this.props.setRepresentativeData}
                            remove={this.props.removeRepresentative}
                            minCount={this.modeConfig() && this.modeConfig().minRC}
                        />
                    </div>
                    <div className="l-col l-col-small-12 l-col-medium-6 l-col-4 u-small-spacing-top-l">
                        <Grantors
                            title={this.props.descriptions.grantorLabel}
                            titlePlural={this.props.descriptions.grantorPluralLabel}
                            data={this.props.grantorsFormData}
                            onChange={this.props.setGrantorData}
                            remove={this.props.removeGrantor}
                            minCount={this.modeConfig() && this.modeConfig().minRMC}
                            grantingDate={this.props.grantingDate}
                            grantingDateErrors={this.props.grantingDateErrors}
                            setGrantingDate={this.props.setGrantingDate}
                        />
                    </div>
                </div>
                {(this.modeConfig() && (this.props.representativesFormData.length < this.modeConfig().maxRC)) ?
                    <OraAddBar label={this.getRepresentativeLabel()} id="add_representative"
                               onAddClick={this.addRepresentative.bind(this)} className="u-spacing-top-l u-box-shadow--s"/>
                    : null
                }
                {(this.modeConfig() && (this.props.grantorsFormData.length < this.modeConfig().maxRMC)) ?
                    <OraAddBar label="Dodaj kolejną osobę udzielającą pełnomocnictwa" id="add_grantor"
                               onAddClick={this.addGrantor.bind(this)} className="u-spacing-top-l u-box-shadow--s"/>
                    : null
                }
            </div>
        );
    }

}

//needed until canonical legalForm codes are established
function legalFormTranslator(legalForm) {
    if (legalForm) {
        if (legalForm.length <= 4) {
            return legalForm;
        } else {
            return {
                ONE_PERSON_ENTERPRISE: "JDG",
                PRIVATE_LIMITED_COMPANY: "SZOO",
                CIVIL_PARTNERSHIP: "SC",
                FOUNDATION: "FUN",
                JOINT_STOCK_COMPANY: "SA",
                GENERAL_PARTNERSHIP: "SJ",
                COMANCIATE_COMPANY: "SK",
                CANTEEN_JOINT_STOCK_COMPANY: "SKA",
                PARTNERSHIP: "SP",
                ASSOCIATION: "STOW"
            }[legalForm];
        }
    }
}


const mockRepresentationModeOptions = [
    {value: "JR", description: "Reprezentant"},
    {value: "WR", description: "Kilku reprezentantów"},
    {value: "JP", description: "Pełnomocnik"},
    {value: "WP", description: "Kilku pełnomocników"}
];

const mockValidRepresentationModes = {
    JDG: ["JR", "JP", "WP"],
    SC: ["JR", "WR", "JP", "WP"]
};

RepresentativeDataComponentView.defaultProps = {
    descriptions: {
        title: "Dane reprezentanta",
        representativeLabel: "Dane reprezentanta",
        representativeLabelPlural: "Dane reprezentantów",
        representativeLabelForProxy: "Dane pełnomocnika",
        representativeLabelForProxyPlural: "Dane pełnomocników",
        grantorLabel: "Dane osoby udzielającej pełnomocnictwa",
        grantorPluralLabel: "Dane osób udzielających pełnomocnictwa"
    },
    representationModeOptions: mockRepresentationModeOptions,
    validRepresentationModes: mockValidRepresentationModes,
    maxRepresentativesCount: 5,
    maxGrantorsCount: 5
};

const mapStateToProps = state => ({
    representativesFormData: getRepresentativesFormData(state),
    grantorsFormData: getGrantorsFormData(state),
    representationMode: getRepresentationMode(state),
    isValid: isRepresentativeDataValid(state),
    isFilled: isRepresentativeDataFilled(state),
    grantingDate: getGrantingDate(state),
    grantingDateErrors: getGrantingDateErrors(state),
    customer: getCustomerData(state)
});

const mapDispatchToProps = dispatch => ({
    setRepresentativeData: ({name, value, index, validate}) => dispatch(changeRepresentativeFormField({name, value, index, validate})),
    setRepresentativeDataNoValidations: ({name, value, index}) => dispatch(changeRepresentativeFormFieldNoValidations({name, value, index})),
    setGrantorData: ({name, value, index, validate}) => dispatch(changeGrantorFormField({name, value, index, validate})),
    setRepresentationMode: (mode, modeConfig) => dispatch(setRepresentationMode(mode, modeConfig)),
    removeGrantor: (index) => dispatch(removeGrantor(index)),
    removeRepresentative: (index) => dispatch(removeRepresentative(index)),
    setGrantingDate: (date) => dispatch(setGrantingDate(date)),
    registerValidation: () => dispatch(registerNextStepCondition("representativeData")),
    requestCartRepresentativeData: () => dispatch(requestCartRepresentativeData()),
    changeRepresentativeCountry: ({id, name, value, index}) => dispatch(changeRepresentativeCountry({id, name, value, index}))
});

const RepresentativeDataComponent = connect(mapStateToProps, mapDispatchToProps)(RepresentativeDataComponentView);

export default RepresentativeDataComponent




